.navbar {
background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
height: 80px;
display: flex;
justify-content: center !important;
align-items: center;
font-size: 1.2rem;
position: fixed !important;
top: 0;
z-index: 999;
width: 100%;
padding: 0 !important;

}

.navbar-container {
display: flex;
justify-content: center;
height: 80px;
max-width: 1500px;
}

a {
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 1.2rem; */
  
}

a:hover {
  text-decoration: none !important;
}
.navbar-logo {
color: #fff;
justify-self: start;
margin-left: 20px;
/* cursor: pointer; */
text-decoration: none;
font-size: 2rem;
display: flex;
align-items: center;
}

.navbar-logo:hover {
  color: #fff;
text-decoration: none;
}

.fa-typo3 {
margin-left: 0.5rem;
font-size: 1.8rem;
}

.nav-menu {
display: grid;
grid-template-columns: repeat(4, auto);
grid-gap: 10px;
list-style: none;
text-align: center;
width: 60vw;
justify-content: end;
/* margin-right: 2rem; */
}

.nav-item {
height: 80px;
display: inline-block;

}

.nav-links {
color: #fff;
display: flex ;
align-items: center;
text-decoration: none; /* not working?? */
padding: 0.5rem 1rem;
height: 100%;
}

.nav-links:hover {
border-bottom: 4px solid #fff;
transition: all 0.2s ease-out;
color: #fff;
text-decoration: none;
}

.fa-bars {
color: #fff;
}

.nav-links-mobile {
display: none;
}

.menu-icon {
display: none;
}
